<template>
    <div class="search">
        <div class="header">
            <span class="back" @click="back">
                <img src="../assets/img/back.png">
            </span>
            <div class="searchBar">
                <input type="text" placeholder="请输入您想搜索的名师/拳馆" autofocus>
                <img src="../assets/img/searchIcon.png">
            </div>
        </div>
    </div>
</template>

<script>
export default {
  name: 'search',
  methods: {
    back () {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    font-size: 0.4rem;
  }

  .search {
    padding: 0 0.4rem;
  }

  .header {
    padding: 0.16rem 0;
    height: 0.96rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #fff;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  .back img {
    width: 100%;
  }

  .searchBar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 8.48rem;
    height: 1rem;
    border: 1px solid #e6e6e6;
    border-radius: 0.5rem;
    margin-left: 0.373333rem;
  }

  .searchBar input {
    outline: none;
    width: 6rem;
    margin-left: 0.4rem;
    border: none;
    outline: none;
  }

  .searchBar img {
    width: 0.48rem;
    height: 0.48rem;
    margin-right: 0.2rem;
  }
</style>



